import uniIndexedList from "@/components/uni-indexed-list/uni-indexed-list.vue"
import amap from '@/static/js/amap-wx.js';
export default {
	components: {
		uniIndexedList
	},
	data() {
		return {
			search: false,
			city: [], //城市
			list: [],
			kesword: '',
			search_list: [],
			height: '',
			top: "",
			style: [],
			myAmap: '',
			now_city:''
		}
	},
	onLoad: function() {
		// console.log(getApp())
		this.myAmap = new amap.AMapWX({
			key: 'fc7b2b1171e49890b952144ed8562195'
		})
	},
	onShow: function() {
		this.getCity()
		this.getHeight()
		this.getAddress()
		
	},
	methods: {
		//获得当前地址
		getAddress: function() {
			this.myAmap.getRegeo({
				location: this.location,
				success:(data)=>{
					console.log(data)
					this.now_city = data[0].regeocodeData.addressComponent.city
				},
				fail: function(info) {
					//失败回调
					//  console.log(info)
				}
			})
		},
		//计算高度
		getHeight: function() {
			let info = uni.createSelectorQuery().select(".mybox");
			info.boundingClientRect((data) => { //data - 各种参数
				this.height = ((getApp().globalData.screenHeight - getApp().globalData.statusBarHeight - getApp().globalData.titleBarHeight) -
					data.height) + 'px'
				this.top = data.height + 'rpx'

			}).exec()
		},

		//获得城市列表
		getCity: function() {
			let that = this;
			uni.showNavigationBarLoading()
			this.$urlRequest('getCityListV2.do', {}).then(res => {
				uni.hideNavigationBarLoading();
				if (this.city == '') {
					this.search = false
				} else {
					this.search = true
				}
				this.list = res
				this.search = false
			})

		},
		getVal: function() {
			if (this.kesword != '') {
				this.$nextTick(() => {
					this.mysearch()
				})
			} else {
				this.$nextTick(() => {
					this.getCity()
				})

			}
		},
		mysearch: function() {
			let that = this;
			uni.showNavigationBarLoading()
			this.$urlRequest('getCityList.do', {
				name: this.kesword
			}).then(res => {
				uni.hideNavigationBarLoading();
				this.search_list = res
				this.search = true
			})

		},
		//选择城市
		bindClick:function(res){
			uni.setStorage({
				key:'name',
				data:res.item.name
			})
			uni.navigateBack({
			  delta:1
			})
		},
		choose_city:function(e){
			uni.setStorage({
				key:'name',
				data:e.currentTarget.dataset.title
			})
			uni.navigateBack({
			  delta:1
			})
		}
	}
}
